/* MainHeaderNavigation Styles */
body {
  padding-top: 65px;
}

.blue-header {
  background: #00447c;
}

.navbar .navbar-collapse {
  text-align: center;
}

.navbar-lower {
  text-align: center;
  color: #fff;
  background-color: #337ab7;
  line-height: 50px;
  font-weight: 600;
  font-size: 1.2em;
  text-decoration: none;
}

.navbar-lower a {
  color: #fff;
  text-decoration: none;
}

.toolbar {
  z-index: 9999;
    .navbar-collapse {
    background: #fff;
  }
  button.navbar-toggle {
    // border: none;
    color: #fff;
    font-size: 25px;
  }
  button.navbar-toggle span.icon-bar {
    color: #fff;
  }
  ul li a {
    color: #00447c;
    padding-left: 7px;
    padding-right: 6px;
    -webkit-transition: all 0.25s ease-in-out;
    -moz-transition: all 0.25s ease-in-out;
    -o-transition: all 0.25s ease-in-out;
    transition: all 0.25s ease-in-out;
  }
  ul li a:hover {
    color: #333;
  }
  navbar-collapse .navbar-collapse.collapse .nav.navbar-nav.navbar-right {
    margin: 0;
  }
  li.active a {
    background: #ecf0f1!important;
    color: #333!important;
  }

}

.navbar {
  min-height: 46px;
  border-radius: 0!important;
  margin-bottom: 0;
}

.navbar-nav {
  margin: 0;
}

.navbar-default {
  border: none;
}

.navbar-collapse {
  padding-right: 0;
  padding-left: 0;
}

.navbar-static-top {
  border-width: 0;
}

.dropdown-menu {
  padding-left: 10px;
  padding-right: 10px;
  font-size: 0.8em;
  padding-top: 0px;
  padding-bottom: 0px;
  text-transform: capitalize;
}

.pushdown-menu {
  // padding-left: 10px;
  padding-right: 10px;
  font-size: 0.8em;
  padding-top: 0px;
  padding-bottom: 0px;
  text-transform: capitalize;
  text-decoration: none;
  text-align: left;
  list-style-type: inherit;
  max-width: 15em;
  padding-left: 0.5em;
  text-indent:-0.6em;
  display: none;
}

li.dropdown {
  color: #00447c;
  font-weight: 800;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 1px;
  text-align: left;
  cursor: pointer;
  border-bottom: 2px solid #4cb4f2;
  font-size: 1.2em;
  text-transform: capitalize;
  padding: 5px 0px 5px 0px;
}

li.dropdown.last {
  border-right: none;
}

.nav > li > a {
  display: inline;
  line-height: 50px;
  font-weight: 800;
  font-size: 1.4em;
}
//begin expanding search css
#content-nav {
  margin-top: 0;
  // height: 40px;
  // margin-bottom: 10px;
  background-color: #fff;
  max-height: 600px;
  transition: max-height 500ms cubic-bezier(0.420, 0.000, 0.580, 1.000);
}

// #content-nav:hover {
//   max-height: 600px;
// }

#content-nav #wrapper li {
  padding-left: 0;
  border-bottom: 2px solid #4cb4f2;
  margin: 5px 0 -5px;
  font-weight: bold;
}

.container-dropdown {
  margin: 0 40px 0 50px;
}

i.fa.fa-chevron-down.pull-right.chevron-pulldown {
  color: #007dc3;
  padding: 2px 0 0;
}

#content-nav #wrapper #nav-search div {
  border: none;
}

#content-nav #wrapper ul {
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
  display: inline-table;
  // width: 985px;
}

#content-nav #wrapper ul li {
  float: left;
  padding: 0;
  // height: 40px;
  color: #FFF;
  font-size: 18px;
  font-family: 'Open Sans';
  font-weight: normal;
  text-decoration: none;
  text-transform: capitalize;
}

#content-nav #wrapper ul li:hover {
  position: relative;
  left: -1px;
  background-color: #E9EDF1;
  color: #00447c;
  cursor: pointer;
}

#content-nav #wrapper ul li.active-section {
  position: relative;
  left: -1px;
  background-color: #ebd0f5; //    color: #9c5fb5;
  cursor: pointer;
}

#content-nav #wrapper ul li.active-nav {
  position: relative;
  left: -1px;
  background-color: #f7f2f9; //    color: #9c5fb5;
}

#content-nav #wrapper ul li a {
  display: block;
  padding: 0 20px;
  // color: #FFF;
  text-decoration: none;
  border-bottom: 0 solid #4cb4f2;
}

#content-nav #wrapper ul li div {
  padding: 0 14px;
  border-right: 1px solid #e6d6ed;
  height: 20px;
  line-height: 1.3em;
  position: relative;
  top: 10px;
}

#content-nav #nav-search-close {
  position: relative;
  top: -78px;
  left: 50em;
  color: #007dc3;
  cursor: pointer;
  font-size: 1.3em;
}

#content-nav .search-input-desktop {
  display: none;
  position: absolute; // was relative
}

#content-nav #search-input div {
  padding-right: 0 !important;
  top: 3px !important;
}

#content-nav #search-input input[type=text] {
  display: inline-block;
  height: 55px;
  width: 32em;
  /* was 900px */
  border: none;
  color: #6E6E6E;
  border: 2px solid #fff;
  border-radius: 5px 5px 5px 5px;
  background-color: #fff;
}

#content-nav #search-input .button {
  display: inline-block;
  padding-left: 3px;
  top: -4.2em;
  left: 66em;
  /*was 808px */
  position: relative;
  height: 2.8em;
  width: 8.0em;
  background-color: #007dc3; //    color: #9c5fb5;
  border-radius: 2;
  letter-spacing: 5px;
  font-weight: 500;
  font-family: 'Raleway', sans-serif;
}

#content-nav #search-input .button:hover {
  color: #fff;
  background-color: #00447c;
  ::-webkit-input-placeholder {
    color: #fff;
    background-color: #00447c;
  }

  :-moz-placeholder {
    /* Firefox 18- */
    color: gray;
  }

  ::-moz-placeholder {
    /* Firefox 19+ */
    color: gray;
  }

  :-ms-input-placeholder {
    color: gray;
  }
}

.button {
  height: 35px;
  line-height: 35px;
  display: block;
  border-radius: 4px; //    background-color: #9c5fb5;
  text-transform: uppercase;
  text-align: center;
  font-family: 'Open Sans';
  font-size: 12px;
  letter-spacing: 1.2px;
  color: #fff;
  border: 0;
}

.button:hover {
  text-decoration: none;
  color: #FFF;
  cursor: pointer;
  font-family: 'Open Sans';
}

.button:focus {
  text-decoration: none;
  color: #FFF;
}

.glyphicon-search {
  color: #007dc3 !important;
  padding-top: 1.2em;
  font-size: 1.2em;
  cursor: pointer;
}

.glyphicon-opened {
  color: #717171 !important;
  padding-top: 10px;
  font-size: 1.2em;
  padding-right: 30px;
  padding-left: 20px;
  padding-bottom: 12px;
  top: 0.77em;
  // background-color: #E9EDF1;
  border-radius: 5px 0 0 5px;
  cursor: unset;
}
/* enable absolute positioning */
.inner-addon {
  position: relative;
}
/* style glyph */
.inner-addon .glyphicon {
  position: absolute;
  padding: 20px 10px 10px 10px;
  pointer-events: none;
}
/* align glyph */
.left-addon .glyphicon {
  left: 1em;
}

.right-addon .glyphicon {
  right: 0;
}
/* add padding  */
.left-addon input {
  padding-left: 70px;
}

.right-addon input {
  padding-right: 30px;
}
/* add back pointer functionality for x-out button  */
.glyphicon-remove {
  pointer-events: all !important;
}
//end expanding search css
.apply-button {
  background: #d11242;
  color: white;
  font-size: 1.6em;
  border-radius: 0 0 10px 10px;
  text-align: center;
  padding: 0.5em 0.5em;
  width: 10%;
  font-family: 'Raleway', sans-serif;
  font-weight: 800;
  letter-spacing: 2px;
  float: right;
  margin-left: 0.1em;
  // display: none;
}

.give-visit-buttons {
  background: gray;
}

.msudenver_logo {
  padding: 15px 0px;
  // max-width:90%;
  max-height:65px;
}

.custom-search-input {
  margin-top: 5px;
  height: 40px;
  padding: 3px;
  border: solid 1px #E4E4E4;
  border-radius: 6px;
  background-color: #fff;
}

.custom-search-input input {
  border: 0;
  box-shadow: none;
}

.custom-search-input button {
  margin: 2px 0 0;
  background: none;
  box-shadow: none;
  border: 0;
  color: #003663;
  padding: 0 8px 0 10px;
  border-left: solid 1px #ccc;
}

.custom-search-input button:hover {
