}

#search-input #search-text {
  height: 14px;
  border-style: none;
  border-color: inherit;
  border-width: 0;
  width: 207px;
  margin-top: 3px;
  font-family: 'Raleway', sans-serif;
  font-size: 1.0em;
  color: #bcbcbc;
  letter-spacing: 2px;
}

#search-input #autoSuggestResults {
  display: none;
  position: absolute;
  z-index: 2;
  width: 794px;
  margin-left: 15px;
  margin-top: 36px;
  background-color: white;
  border: 1px solid #999;
  box-shadow: 3px 3px 3px #AAA;
  padding-top: 3px;
  padding-bottom: 3px;
  font-family: Verdana;
  font-size: 8pt;
  color: #999;
  border-bottom-right-radius: 2px;
  border-bottom-left-radius: 2px;
}

#autoSuggestResults .autoSuggestResult {
  padding: 3px 19px 3px 11px;
  cursor: default;
}

#autoSuggestResults .autoSuggestResultMouseOver {
  background-color: #EEE;
}

.gobutton #searchGoLink.disableGoButton {
  visibility: hidden;
}

.fa-bars {
  background-color: #005fae;
  padding: 20px;
  margin: -17px -30px -16px -16px;
}

.glyphicon-search-mobile {
  color: #fff !important;
  font-size: 20px !important;
  display: inline-block;
  float: right;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 23px;
}

.quicktools {
  border-bottom: 2px solid #4cb4f2;
}

.chevron-pulldown {
